      this.lightBoxImage = this.pjArchives[nextImg].src;
      this.lightBoxAlt = this.pjArchives[nextImg].alt;
    },
    pjaLbPrev: function(){
      let nextImg = (this.imgIndex --) % this.pjArchives.length;
